.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.List {
  display: flex;
  justify-content: space-around;
  border: 1px solid black;
  height: 100vh;
  overflow: scroll;
}
.New {
  /* border-right: 1px solid black; */
  min-width: 400px;
  max-width: 70%;
}
.list-container {
  width: 50%;
}
.pastItem {
  text-decoration: line-through;
}

.btn {
  background-color: rgb(228, 228, 228);
  border: 1px solid rgb(228, 228, 228);
  border-radius: 20px;
  padding: 5px 20px;
  font-weight: 300;
}
.reset-btn {
  margin-bottom: 20px;
  margin-top: 10px;
}

.inputField {
  width: 350px;
  margin-right: 20px;
  padding: 5px;
  border: .5px solid white;
  border-radius: 10px;
  outline: none;
}
.title {
  margin-top: 0;
  font-size: 55px;
  /* text-transform: uppercase; */
}
.list-title {
  background-color: #282c3452;
  margin-top: 0;
  text-transform: uppercase;
}
